:root {
  --white: white;
  --black: #2c2c2c;
  --dark-text: #35363f;
  --gray-dark: #707070;
  --gray-med: #959595;
  --gray-lt: #c2c0c0;
  --gray-lightest: #fbfbfb;
  --dusk: #435363;
  --fog: #607774;
  --storm: #4f869f;
  --teal: #85c4ca;
  --gold: #ffd91f;
  --green: #4d9b5e;
  --red: #b31b1b;
  --gray: #c2c0c0;
  --gray-light: #d9d9d9;
  --near-white: #fbfbfb;
  --xs: 320px;
  --small: 480px;
  --medium: 782px;
  --large: 1224px;
  --xl: 1432px;
  --xxl: 1640px;
  --font-sans: "canada-type-gibson", sans-serif;
  --font-serif: "freight-micro-pro", verdana;
  --font-curly: "Alisha";
  --base-font-size: 20px;
  --medium-font-size: 20px;
  --small-font-size: 16px;
  --sidebar-font-size: 20px;
  --main-font-size: 28px;
  --font-size--huge: 60px;
  --light: 300;
  --regular: 400;
  --semi: 600;
  --heavy: 800;
  --border-width: 1px;
  --border-color: var(--gray-light);
  --border-radius: 4px;
  --space: 1rem;
  --mainGutter: var(--space);
  --auto-grid-min-size: calc(10 * var(--space));
  --grid-gap: var(--mainGutter);
  --base-grid-column: 84px;
  --component-bottom-margin: calc(1 * var(--space));
}

@media (min-width: 1224px) {
  :root {
    --base-font-size: 24px;
  }
}

@media (min-width: 782px) {
  :root {
    --space: 1.5rem;
  }
}

@media (min-width: 480px) {
  :root {
    --mainGutter: var(--space);
  }
}

@media (min-width: 782px) {
  :root {
    --mainGutter: calc(2 * var(--space));
  }
}

/* -------------------------

Title: floats
Descr: 
    floats, pulls and full widths. Basically anything that will need to make a component or page item break out of the flow of the page or its container

--------------------------- */
.floatLeft {
  float: left;
  margin-left: 0;
}

.floatRight {
  float: right;
  margin-right: 0;
}

.align-left {
  margin-right: 1rem;
}

.align-right {
  margin-left: 1rem;
}

@media (min-width: 782px) {
  .pullLeft {
    float: left;
    margin-left: -2rem;
    margin-right: 2rem;
  }
  .pullRight {
    float: right;
    margin-right: -2rem;
    margin-left: 2rem;
  }
  .stretch {
    margin: 1rem -3rem;
  }
}

.full-width {
  width: calc(100% + (2 * var(--mainGutter)));
  margin-left: calc(-1 * var(--mainGutter));
  text-align: center;
}

/* -------------------------

Title: container layouts.
Descr: 
    this is the container that spans from the <header> to <footer>. It establishes columns for sidebars etc. .mainContent(--modifier) is a child of .container.

    - --has-aside and has--sidebar are the base models. Basic page, and TOC both use them

    -- article and --hasPano are for articles, more complicated grids

--------------------------- */
.container {
  grid-column: 2;
  display: grid;
  align-items: flex-start;
  align-content: flex-start;
}

@media (min-width: 1224px) {
  .container--has-aside {
    grid-gap: 2rem;
    grid-template-columns: 1fr minmax(200px, 300px);
    grid-template-areas: "title title" "components aside";
  }
  .container--has-aside .title {
    grid-area: title;
  }
  .container--has-aside .components {
    grid-area: components;
  }
  .container--has-aside aside {
    grid-area: aside;
  }
}

.container--has-sidebar .toc-menu, .container--has-sidebar .sidebar-nav {
  grid-area: menu;
}

.container--has-sidebar .sidebar {
  grid-area: sidebar;
}

.container--has-sidebar main {
  grid-area: main;
}

@media (min-width: 1640px) {
  .container--has-sidebar {
    grid-gap: calc( 3 * var(--space));
    grid-row-gap: var(--space);
  }
}

/* -------------------------

Title: <main> layouts.
Descr: 
    main content is the <main> tag. It is where the main components are. It is a child of the .container.

--------------------------- */
.main {
  max-width: var(--xl);
  margin: 0 auto;
  display: grid;
  grid-gap: 2rem;
  grid-row-gap: 0;
  padding: 0 1rem;
}

@media (min-width: 782px) {
  .main {
    grid-template-columns: repeat(12, calc(8.59% - 2rem));
  }
}

.basic > div,
.landingPage > div {
  grid-column: 1 / -1;
}

.basicSidebar {
  grid-template-rows: auto auto 1fr;
  grid-template-areas: "nav" "title" "content" "sidebar";
}

.basicSidebar .siteNav {
  grid-area: nav;
}

.basicSidebar .title {
  grid-area: title;
}

.basicSidebar .sidebar {
  grid-area: sidebar;
}

.basicSidebar .content {
  grid-area: content;
}

.basicSidebar aside {
  grid-area: aside;
}

@media (min-width: 782px) {
  .basicSidebar {
    grid-template-areas: "nav nav nav nav title title title title title title title title" "nav nav nav nav content content content content content content content content" "sidebar sidebar sidebar sidebar content content content content content content content content";
  }
}

@media (min-width: 782px) {
  .basicSidebar--has-aside {
    grid-template-areas: "nav nav nav nav title title title title title title title title" "nav nav nav nav content content content content content content aside aside" "sidebar sidebar sidebar sidebar content content content content content content aside aside";
  }
}

@media (min-width: 782px) {
  .page--toc.page--has-aside {
    grid-template-areas: "nav nav nav nav title title title title title title title title" "nav nav nav nav content content content content content content content content" "aside aside aside aside content content content content content content content content";
  }
}

.page-title {
  grid-area: page-title;
  display: block;
}

.main-content {
  grid-area: content;
}

/* -------------------------

Title: componentWrappers
Descr: 
    Takes care of any universal component wrapper stylings

--------------------------- */
.components > span {
  display: block;
  margin-bottom: 4rem;
}

.pageComponent {
  margin-bottom: var(--component-bottom-margin);
  clear: both;
}

/* 
  Base styles for all figures
  - basic figure
  - figure based on different view modes
  - extra class based on image style to set max widths
*/
figure {
  margin: 1rem 0;
  background: var(--gray-lightest);
  width: 100%;
  border-radius: var(--border-radius);
}

figure.figure--pano {
  margin: 0;
}

figure.figure--thumbnail {
  background: none;
}

.pageComponent--ftb .figure--6_4_newsletter {
  max-width: 600px;
}

.pageComponent--ftb .figure--5_4 {
  max-width: 520px;
}

.pageComponent--ftb .figure--4_5 {
  max-width: 300px;
}

figcaption {
  font-weight: var(--light);
  font-size: var(--medium-font-size);
  padding: 1rem;
}

.multiple--grid {
  display: grid;
  grid-gap: var(--space);
}

.multiple--flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: "flex-start";
}

/* -------------------------

Title: Swiss Army 
Descr: Where all little helper classes that don't go on one specific component css will live. They all must be applicable to multiple elements.

--------------------------- */
.hidden {
  display: none;
}

.sr-only {
  display: block;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(1px);
  white-space: nowrap;
  position: absolute;
}

.animate-fade {
  -webkit-transition: opacity 0.3s ease-out;
  -moz-transition: opacity 0.3s ease-out;
  -ms-transition: opacity 0.3s ease-out;
  -o-transition: opacity 0.3s ease-out;
  transition: opacity 0.3s ease-out;
}

.animate-fade:hover {
  opacity: 0;
}

.animate-move > .demo-shape {
  -webkit-transition: all 0.8s ease-in-out;
  -moz-transition: all 0.8s ease-in-out;
  -ms-transition: all 0.8s ease-in-out;
  -o-transition: all 0.8s ease-in-out;
  transition: all 0.8s ease-in-out;
}

.sg-main {
  max-width: 1640px;
  width: 100%;
  margin: 0 auto;
  padding: 0 1.5rem;
}

@media (min-width: 480px) {
  .sg-main {
    padding: 0 1.5rem;
  }
}

@media (min-width: 1640px) {
  .sg-main {
    padding-left: calc(1.5rem + calc(-50vw + calc(1640px / 2)));
    padding-right: calc(1.5rem + calc(-50vw + calc(1640px / 2)));
  }
}

.pl-template {
  display: flex;
  flex-flow: column nowrap;
}

.pl body,
.sg-main body {
  background: var(--white);
}

.pl .sg-subtype h2,
.sg-main .sg-subtype h2 {
  font-size: 3rem;
  text-transform: uppercase;
  background-color: #4d9b5e;
  margin: 0 calc(-50vw + 50%) 1.5rem calc(-50vw + 50%);
}

.pl .sg-subtype h2 a,
.sg-main .sg-subtype h2 a {
  max-width: 1640px;
  width: 100%;
  margin: 0 auto;
  padding: 1.5rem 1.5rem;
  display: block;
  color: white;
  text-decoration: none;
  line-height: 1;
}

@media (min-width: 480px) {
  .pl .sg-subtype h2 a,
  .sg-main .sg-subtype h2 a {
    padding: 1.5rem 1.5rem;
  }
}

@media (min-width: 1640px) {
  .pl .sg-subtype h2 a,
  .sg-main .sg-subtype h2 a {
    padding-left: calc(1.5rem + calc(-50vw + calc(1640px / 2)));
    padding-right: calc(1.5rem + calc(-50vw + calc(1640px / 2)));
  }
}

.pl .sg-pattern-title,
.sg-main .sg-pattern-title {
  color: #738ba3;
  font-size: 1.4rem !important;
  text-transform: none !important;
  font-family: "canada-type-gibson", sans-serif;
}

.pl .sg-pattern-title a,
.sg-main .sg-pattern-title a {
  color: #b31b1b !important;
  font-size: 2.2rem;
}

.pl .sg-pattern-extra-info,
.sg-main .sg-pattern-extra-info {
  width: 100%;
}

.pl .sg-pattern-extra-code,
.sg-main .sg-pattern-extra-code {
  display: none;
}

.pl .visually-hidden,
.sg-main .visually-hidden {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  height: 1px;
  width: 1px;
  word-wrap: normal;
}

.pl .sg-colors,
.sg-main .sg-colors {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}

.pl .sg-colors li,
.sg-main .sg-colors li {
  flex: 0 0 100px;
  margin: 0 0.5rem 1rem;
}

.pl .sg-colors__sample,
.sg-main .sg-colors__sample {
  display: block;
  height: 100px;
  margin-bottom: 0.3em;
}

/* -------------------------

Title: headings.
Descr: 
    Sets basic styles for h1-h4

--------------------------- */
h1, h2, h3, h4 {
  font-family: "canada-type-gibson", sans-serif;
  font-weight: 600;
  line-height: 1;
  margin: .5em 0 .5em 0;
}

h1 {
  font-size: calc(2.5 * var(--base-font-size));
}

h2 {
  font-size: calc(2 * var(--base-font-size));
}

h3 {
  font-size: calc(1.75 * var(--base-font-size));
  font-weight: var(--regular);
}

h4 {
  font-size: calc(1.5 * var(--base-font-size));
}

h5,
h6 {
  font-family: "canada-type-gibson", sans-serif;
  font-size: 28px;
  color: var(--black);
  font-weight: 600;
  line-height: 32px;
}

.h1--border, .h2--border, .h3--border, .h4--border {
  border-bottom: 1px solid var(--teal);
  margin: 0 auto 1em auto;
}

.sidebar h2 {
  font-size: calc(1.5 * var(--base-font-size));
}

/* -------------------------

Title: _text.scss
Descr: 
      - Basic page text definitions
      - Special display class definitions

--------------------------- */
html {
  font-size: var(--base-font-size);
}

body {
  font-family: "canada-type-gibson", sans-serif;
  font-size: var(--base-font-size);
  color: var(--black);
  font-weight: var(--regular);
  line-height: 1.4;
  overflow-x: hidden;
}

p {
  margin: 0 0 1em;
}

.blockquote {
  font-style: italic;
  border-left: solid 3px var(--gray-lt);
  margin-left: 1em;
  padding-left: 1em;
}

.hr {
  border-style: solid;
  border-width: 1px 0 0;
  color: currentColor;
}

/*
  Intros
*/
.intro {
  font-size: 1.5rem;
  text-align: center;
  max-width: 1200px;
}

.intro[class$="--curly"] {
  font-family: var(--font-curly);
  font-size: 4rem;
  line-height: .75;
  margin: 0 auto 4rem;
  transform: rotate(-6deg);
  filter: drop-shadow(-1px -1px 0 var(--gold) drop-shadow(1px 1px 0 var(--green)));
}

/* -------------------------

Title: link
Descr: 
    basic link

--------------------------- */
a, .link {
  color: var(--red);
  text-decoration-color: var(--gray-lt);
}

/* -------------------------

Title: image
Descr: 
    Most basic universal image styles

--------------------------- */
img,
picture {
  display: block;
  height: auto;
  max-width: 100%;
}

/* -------------------------

Title: icons.scss
Descr: styles up all icons

--------------------------- */
.icon--arrow {
  width: 1rem;
}

.icon--bars {
  width: 2rem;
}

.form-item__dropdown {
  border: 1px solid #f4f4f4;
  display: block;
  position: relative;
}

.form-item__dropdown::after {
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 9px solid #d9d9d9;
  content: " ";
  position: absolute;
  top: 42%;
  right: 1em;
  z-index: 2;
  /* These hacks make the select behind the arrow clickable in some browsers */
  pointer-events: none;
  display: none;
}

.form-item__dropdown:hover {
  border-color: #35363f;
}

.form-item__select {
  border: 1px solid #f4f4f4;
  height: 41px;
  font-size: 16px;
  margin: 0;
  outline: none;
  padding: 0.6em 0.8em 0.5em;
  width: 100%;
}

.form-item__select :focus {
  outline: none;
  color: #222;
}

@supports (-webkit-appearance: none) or (appearance: none) or ((-moz-appearance: none) and (mask-type: alpha)) {
  /* Show custom arrow */
  .form-item__dropdown::after {
    display: block;
  }
  /* Remove select styling */
  .form-item__select {
    padding-right: 2em;
    /* Match-01 */
    /* inside @supports so that iOS <= 8 display the native arrow */
    background: none;
    /* Match-04 */
    /* inside @supports so that Android <= 4.3 display the native arrow */
    border: 1px solid transparent;
    /* Match-05 */
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }
  .form-item__select:focus {
    border-color: #999;
    border-radius: 0;
  }
}

/* -------------------------

Title: Buttons
Descr: 
   - pill links
   - js trigger buttons / icon buttons

--------------------------- */
.button {
  border: 2px solid var(--black);
  padding: .5em 1em;
  color: var(--black);
  border-radius: var(--border-radius);
  background: none;
}

.button.prev span, .button.next span {
  content: "";
  font-size: 1rem;
  width: 35px;
  line-height: 35px;
  top: 1px;
  right: 0;
  text-align: center;
  text-indent: 0;
  color: #35363f;
  cursor: pointer;
}

.button.prev span {
  transform: rotate(90deg);
}

.button.next span {
  transform: rotate(-90deg);
}

.iconButton svg {
  width: 1rem;
}

/* -------------------------

Title: tables
Descr: 
    Base table styles

--------------------------- */
.table, .text-long table {
  border: 1px solid var(--gray-light);
  border-radius: 4px;
  border-spacing: 0;
  background-color: var(--gray);
  width: 100%;
}

.table__heading-cell, .text-long th {
  border: none;
  border-left: 1px solid var(--gray-light);
