/* -------------------------

Title: Cards
Descr: Sets grid default card styles
Variations: 
  card--small
  card--article
  card--feature
  card--stat

--------------------------- */
.card {
  box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.08);
  border-radius: var(--border-radius);
  margin-bottom: var(--space);
}

.card__title {
  font-size: 1rem;
  line-height: 1.3;
  font-weight: var(--semi);
}

.card__title-link {
  color: var(--black);
}

.card__label {
  margin-bottom: 0;
  color: var(--gray-dark);
}

.card__body {
  margin: 0.7em 0 1em;
}

.card__button {
  border: 2px solid var(--black);
  padding: .5em 1em;
  color: var(--black);
  border-radius: var(--border-radius);
  background: none;
}

.card--small {
  display: grid;
  grid-gap: var(--space);
  grid-template-columns: 80px 1fr;
}

.card--small .card__title {
  font-family: "canada-type-gibson", sans-serif;
  font-size: var(--medium-font-size);
  color: var(--black);
  font-weight: var(--regular);
  line-height: 1.4;
}

.card--small .card__label,
.card--small .card__byline {
  display: none;
}

.card--small .card__date.card__date {
  text-align: left;
}

[data-embed-button="article_entity_embed"] .card--small {
  max-width: 400px;
}

.card--article {
  grid-row: span 2;
}

.card--article .card__copy {
  padding: 1rem;
  display: grid;
  grid-gap: 0px;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "label label date" "title title title" "byline byline social";
}

.card--article .card__title {
  grid-area: title;
  margin-top: 0.5rem;
  margin-bottom: 1.5rem;
}

.card--article .card__label {
  grid-area: label;
  font-family: var(--font-serif);
  font-size: var(--medium-font-size);
  color: var(--black);
  font-weight: var(--regular);
  line-height: 1.4;
}

.card--article .card__date {
  grid-area: date;
  text-align: right;
  font-size: var(--small-font-size);
}

.card--article .card__byline {
  grid-area: byline;
  font-size: var(--small-font-size);
}

.card--article.card--small {
  border: none;
  margin-bottom: 2rem;
}

.card--article.card--small .card__copy {
  padding: 0.5rem 0.5rem 1rem 0;
  display: grid;
  grid-gap: 0px;
  grid-template-columns: 1fr;
  grid-template-areas: "date" "title";
}

.card--article.card--small .card__title {
  grid-area: title;
  margin: 0;
  font-family: "canada-type-gibson", sans-serif;
  font-size: var(--medium-font-size);
  color: var(--black);
  font-weight: var(--semi);
  line-height: 1.4em;
}

.card--article.card--small .label,
.card--article.card--small .byline {
  display: none;
}

.card--article.card--small .date {
  grid-area: date;
  text-align: left;
  margin-bottom: 0.25rem;
}

.card--article.card--small img {
  border-radius: var(--border-radius);
}

.card--feature {
  display: grid;
  grid-gap: 0px;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "top top" "label dateline" "byline social";
}

.card--feature a {
  color: white;
  margin: 1.5rem;
  padding: 1rem;
  text-align: center;
  border: 4px solid white;
  font-weight: 600;
  font-size: var(--font-size--huge);
  position: relative;
  z-index: 1;
}

.card--feature a:before {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: black;
  content: "";
  opacity: 0.5;
  z-index: -1;
}

.card--feature .card__image {
  grid-area: top;
}

.card--feature .card__title {
  grid-area: top;
  display: flex;
  align-items: flex-end;
}

.card--feature .card__label {
  grid-area: label;
  font-family: var(--font-serif);
  font-size: var(--base-font-size);
  color: var(--black);
  font-weight: var(--regular);
  line-height: 1.4;
  font-size: var(--medium-font-size);
  margin: 20px 1rem;
}

.card--feature .card__date {
  grid-area: dateline;
  text-align: right;
  font-size: var(--small-font-size);
  margin: 20px 1rem;
}

.card--feature .card__byline {
  grid-area: byline;
  font-size: var(--small-font-size);
  margin: 0 1rem 1rem;
}

.card--stat {
  padding: 1rem;
  text-align: center;
  grid-row: span 1;
}

.card--stat .card__title {
  display: none;
}

.card--stat .card__label {
  font-family: "canada-type-gibson", sans-serif;
  font-size: 2rem;
  color: var(--black);
  font-weight: var(--heavy);
  line-height: 1.4;
  border-bottom: solid 4px var(--green);
}

.card--stat .card__body {
  font-weight: var(--semi);
}

.card--course {
  padding: 1rem;
}

.pager {
  margin: 2em 0;
}

.pager__items {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}

.pager__item {
  display: inline-block;
  margin: 0 0.7em;
}

@media (min-width: 1224px) {
  .pager__item {
    margin: 0 1em;
  }
