Descr: Sets up containers for instagram lists, and single instagram conents

--------------------------- */
.insta {
  grid-template-columns: repeat(12, minmax(var(--base-grid-column), 1fr));
  grid-template-rows: repeat(12, minmax(var(--base-grid-column), 1fr));
  background: radial-gradient(var(--teal), transparent 600px);
}

.insta .insta__info {
  grid-column: 1 / 3;
  grid-row: 2 / span 3;
}

.insta .insta__item {
  transition: z-index 3 ease;
  grid-column: 3 / span 4;
  grid-row: 1 / span 4;
}

.insta .insta__item:nth-of-type(2) {
  grid-column: 6 / span 4;
  grid-row: 3 / span 4;
  z-index: 1;
}

.insta .insta__item:nth-of-type(3) {
  grid-column: 9 / span 4;
  grid-row: 2 / span 4;
  z-index: 3;
}

.insta .insta__item:nth-of-type(4) {
  grid-column: 2 / span 4;
  grid-row: 4 / span 4;
  z-index: 5;
}

.insta .insta__item:nth-of-type(5) {
  grid-column: 5 / span 4;
  grid-row: 6 / span 4;
  z-index: 2;
}

.insta .insta__item:nth-of-type(6) {
  grid-column: 8 / span 4;
  grid-row: 7 / span 4;
  z-index: 6;
}

.insta .insta__item:hover {
  animation: fadeinout .3s linear 1 forwards;
}

@keyframes fadeinout {
  0% {
    opacity: 1;
  }
  10% {
    opacity: 0;
  }
  11% {
    z-index: 7;
  }
  50% {
    opacity: .9;
  }
  100% {
    opacity: 1;
  }
}

