    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 1fr 7.5rem min-content;
  }
  .group--photoText--featured .photoText__image {
    grid-column: 1 / span 10;
  }
  .group--photoText--featured .photoText__text {
    grid-column: 8 / span 6;
  }
  .group--photoText--featured .photoText:nth-of-type(2n + 2) .photoText__image {
    grid-column: 3 / span 10;
  }
  .group--photoText--featured .photoText:nth-of-type(2n + 2) .photoText__text {
    grid-column: 1 / span 6;
  }
  .group--photoText--featured .photoText:nth-of-type(3n + 3) .photoText__image {
    grid-column: 2 / span 10;
  }
  .group--photoText--featured .photoText:nth-of-type(3n + 3) .photoText__text {
    grid-column: 5 / span 6;
  }
}

